import S from "./styled";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import settingCookie from "../../utils/settingCookie";
import { useDispatch } from "react-redux";
import Logout from "./Logout";
import authClient from "../../apis/authClient";
import noAuthClient from "../../apis/noAuthClient";
import CircularProgress from "@mui/material/CircularProgress";

import { useSelector } from "react-redux";

function MyPage() {
  //  
  const [reviewRank, setReviewRank] = useState([]);
  //  
  const [scoreRank, setScoreRank] = useState([]);
  //    
  const [isLoading, setIsLoading] = useState(true);
  const [selectedData, setSelectedData] = useState(null);

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const userId = parseInt(localStorage.getItem("user-id"));

  //   
  const decodeBase64 = (base64) => {
    const binaryString = window.atob(base64);
    const bytes = new Uint8Array(binaryString.length);
    for (let i = 0; i < binaryString.length; i++) {
      bytes[i] = binaryString.charCodeAt(i);
    }
    return URL.createObjectURL(new Blob([bytes.buffer], { type: "image/png" }));
  };

  //console.log(typeof userId);

  //     api 
  useEffect(() => {
    const getReviewRanking = async () => {
      try {
        const response = await noAuthClient({
          method: "get",
          url: `/drinks/rankings/review`,
        });
        if (response) {
          setReviewRank(response.data.drinks);
          setIsLoading(false);
          console.log(response.data);
        }
      } catch (error) {
        console.error(error);
      }
    };

    const getScoreRanking = async () => {
      try {
        const response = await noAuthClient({
          method: "get",
          url: `/api/drinks/rankings/rating`,
        });
        if (response) {
          setScoreRank(response.data.drinks);
          console.log(response.data);
        }
      } catch (error) {
        console.error(error);
      }
    };

    // API   
    getReviewRanking();
    getScoreRanking();
  }, []);

  //     
  const checkJuryuInfo = async (e, juryuId) => {
    e.preventDefault();
    navigate("/juryuInfo", { state: { juryuId } });

    try {
      const res = await noAuthClient({
        method: "get",
        url: `/api/drinks/${juryuId}`,
      });
      if (res) {
        console.log(res);
      } else {
        console.log("res");
      }
    } catch (error) {
      if (error.response) {
        const err = error.response.data;
        console.log(err);
      }
    }
  };

  const checkHistory = async (e) => {
    e.preventDefault();
    navigate("/history");

    try {
      const res = await authClient({
        method: "get",
        url: "/api/analyze",
      });
      console.log(res);
    } catch (error) {
      if (error.response) {
        const err = error.response.data;
        console.log(err);
      } else {
        //     
        console.log("Error:", error.message);
      }
    }

    //  
    try {
      const response = await authClient({
        method: "get",
        url: `/api/member`,
      });
      console.log("");
      console.log("member api response:", response);
    } catch (error) {
      if (error.response) {
        const err = error.response.data;
        console.log(err);
      }
    }
  };

  const changeNick = () => {
    navigate("/nickChange");
  };

  const userName = localStorage.getItem("nickname");

  const handleReviewData = () => {
    setSelectedData(reviewRank);
  };

  const handleScoreData = () => {
    setSelectedData(scoreRank);
  };
  const MyPageView = (
    <S.Container>
      <S.Info>
        <S.LogoutButton onClick={checkHistory}> </S.LogoutButton>

        <S.LogoutButton type="button" onClick={changeNick}>
           
        </S.LogoutButton>
        <Logout />
      </S.Info>
      <S.Wrapper>
        <S.Form>
          {userName}    ? Be TOP10  
          !
          {isLoading ? (
            <S.juruBox
              style={{
                paddingTop: "20px",
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <CircularProgress />
            </S.juruBox>
          ) : (
            <>
              <S.ButtonContainer>
                <S.SubmitButton onClick={handleReviewData}>
                  
                </S.SubmitButton>
                <S.SubmitButton onClick={handleScoreData}>
                  
                </S.SubmitButton>
              </S.ButtonContainer>
              <S.juruBox>
                <S.JuruBoxContainer>
                  {selectedData &&
                    selectedData.map((drink, index) => (
                      <S.ReviewBox
                        key={index}
                        onClick={(e) => checkJuryuInfo(e, drink.id)}
                      >
                        <S.Image
                          src={decodeBase64(drink.image)}
                          alt=""
                        />
                        <h5>{drink.name}</h5>
                      </S.ReviewBox>
                    ))}
                </S.JuruBoxContainer>
              </S.juruBox>
            </>
          )}
        </S.Form>
      </S.Wrapper>
    </S.Container>
  );

  return MyPageView;
}

export default MyPage;
