import S from "./styled";
import { useState, useEffect } from "react";
import { useNavigate, useLocation, Link } from "react-router-dom";
import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import NativeSelect from "@mui/material/NativeSelect";
import FormControl from "@mui/material/FormControl";
import SearchIcon from "@mui/icons-material/Search";
import IconButton from "@mui/material/IconButton";
import CircularProgress from "@mui/material/CircularProgress";
import noAuthClient from "../../apis/noAuthClient";

function Dictionary() {
  const navigate = useNavigate();
  const location = useLocation();
  const [jwtToken, setJwtToken] = useState("");
  const [imagePreview, setImagePreview] = useState(null);
  const [selectedFile, setSelectedFile] = useState(null);
  const [jsonData, setJsonData] = useState(null);
  //   useState   
  const [searchTerm, setSearchTerm] = useState("");
  //  
  const [selectedCategory, setSelectedCategory] = useState("");
  // 
  const [decodedImage, setDecodedImage] = useState(null);
  //   
  const [drinkInfo, setDrinkInfo] = useState(null);
  const [drinkInfoList, setDrinkInfoList] = useState([]);
  const [isLoading, setIsLoading] = useState(false); // Add isLoading state

  //   
  const decodeBase64 = (base64) => {
    const binaryString = window.atob(base64);
    const bytes = new Uint8Array(binaryString.length);
    for (let i = 0; i < binaryString.length; i++) {
      bytes[i] = binaryString.charCodeAt(i);
    }
    return URL.createObjectURL(new Blob([bytes.buffer], { type: "image/png" }));
  };

  useEffect(() => {
    if (drinkInfo) {
      const decodedImage = decodeBase64(drinkInfo.image);
      setDecodedImage(decodedImage);
    }
  }, [drinkInfo]);

  //   
  const divideArray = (array, size) => {
    const dividedArray = [];
    while (array.length > 0) {
      dividedArray.push(array.splice(0, size));
    }
    return dividedArray;
  };

  // drinkInfoList 7  
  const dividedDrinkInfoList = divideArray(drinkInfoList, 7);

  const handleSearch = async (e) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      if (searchTerm !== "") {
        const nameRes = await noAuthClient({
          method: "get",
          url: `/api/drinks/name/${searchTerm}`, //     API 
        });
        console.log("Name response:", nameRes);
        console.log(searchTerm);
        console.log(nameRes.data.drinks.length);

        setDrinkInfoList(nameRes.data.drinks);

        //    
        // const decodedImage = decodeBase64(nameRes.data.drinks[0].image);
        // setDecodedImage(decodedImage);
      } else if (selectedCategory !== "ALL") {
        const typeRes = await noAuthClient({
          method: "get",
          url: `/api/drinks/type/${selectedCategory}`, //    API 
        });

        console.log("Name response:", typeRes);
        //console.log(searchTerm);
        //console.log(nameRes.data.drinks.length);

        setDrinkInfoList(typeRes.data.drinks);
        //setDrinkInfo(typeRes.data.drinks[typeRes.data.drinks.length - 1]);
        // const decodedImage = decodeBase64(typeRes.data.drinks[0].image);
        // setDecodedImage(decodedImage);
      }
    } catch (error) {
      if (error.response) {
        const err = error.response.data;
        console.log(err);
      }
    } finally {
      setIsLoading(false);
    }
  };

  const checkJuryuInfo = async (e, juryuId) => {
    e.preventDefault();
    navigate("/juryuInfo", { state: { juryuId } });

    try {
      const res = await noAuthClient({
        method: "get",
        url: `/api/drinks/${juryuId}`,
      });
      if (res) {
        console.log(res);
      } else {
        console.log("res");
      }
    } catch (error) {
      if (error.response) {
        const err = error.response.data;
        console.log(err);
      }
    }
  };

  useEffect(() => {
    setDrinkInfoList([]);
  }, [searchTerm, selectedCategory]);

  //    onChange 
  const handleChange = (e) => {
    setSearchTerm(e.target.value);
  };

  // 
  const handleCategoryChange = (e) => {
    setSelectedCategory(e.target.value);
  };

  //const dictionaryData = location.state?.dictionaryData;

  return (
    <S.Container>
      <S.Wrapper>
        <S.Info>
          <S.searchBox>
            <Box
              component="form"
              sx={{
                display: "flex",
                alignitems: "center",
                justifyContent: "center",
                "&.MuiTextField-root": { height: "100%" },
                "&.MuiInputBase=root": { height: "100%" },
              }}
              noValidate
              autoComplete="off"
            >
              <FormControl sx={{ m: 1, width: "15ch", alignitems: "center" }}>
                <NativeSelect
                  defaultValue={"none"}
                  onChange={handleCategoryChange}
                  inputProps={{ name: "category", id: "uncontrolled-native" }}
                >
                  <option value={"ALL"}></option>
                  <option value={"SOJU"}></option>
                  <option value={"BEER"}></option>
                  <option value={"WINE"}></option>
                  <option value={"LIQUEUR"}></option>
                  <option value={"WHISKEY"}></option>
                  <option value={"FRUIT"}></option>
                  <option value={"YAKJU"}></option>
                  <option value={"BRANDY"}></option>
                  <option value={"RICE_WINE"}></option>
                  <option value={"MAKGEOLLI"}></option>
                </NativeSelect>
              </FormControl>
              <TextField
                id="standard-search"
                label="  !"
                type="search"
                variant="standard"
                alignitems="center"
                InputProps={{
                  disableUnderline: true,
                }}
                sx={{ m: 1, width: "60ch", height: "100%" }}
                value={searchTerm} //      
                onChange={handleChange} // onChange  
              />
              <IconButton
                type="submit"
                onClick={handleSearch}
                aria-label="search"
              >
                <SearchIcon />
              </IconButton>
            </Box>
          </S.searchBox>
        </S.Info>
        {isLoading ? (
          <S.juruBox
            style={{
              paddingTop: "20px",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <CircularProgress />
          </S.juruBox>
        ) : (
          <S.juruBox style={{ paddingTop: "20px" }}>
            {dividedDrinkInfoList.map((line, index) => (
              <div
                key={index}
                style={{
                  display: "flex",
                  alignitems: "center",
                  marginLeft: "10px",
                }}
              >
                {line.map((drinkInfo) => (
                  <S.WhiteBox
                    key={drinkInfo.id}
                    onClick={(e) => checkJuryuInfo(e, drinkInfo.id)}
                  >
                    <S.Image
                      src={decodeBase64(drinkInfo.image)}
                      alt=" "
                    />
                    <S.Text>{drinkInfo.name}</S.Text>
                  </S.WhiteBox>
                ))}
              </div>
            ))}
          </S.juruBox>
        )}
      </S.Wrapper>
    </S.Container>
  );
}

export default Dictionary;
