import { useEffect, useState } from "react";
import { useNavigate, withRouter } from "react-router-dom";
// import axios from "axios";
import { Cookies } from "react-cookie";
import S from "./styled";
import logo from "../../image/logo2.png";
import { useDispatch } from "react-redux";
import jwt_decode from "jwt-decode";
import { GET_NAME } from "../../reducer/nameSlice";
import noAuthClient from "../../apis/noAuthClient";
import authClient from "../../apis/authClient";

const { Kakao } = window;

function Login() {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [isLogin, setIsLogin] = useState(false);
  const [jwtToken, setJwtToken] = useState("");

  // snsLogin
  const snsLogin = async (kakaoToken) => {
    try {
      const res = await noAuthClient({
        method: "get",
        url: `/auth/login?token=${kakaoToken}`,
      });
      console.log("res: ", res);

      const cookie = new Cookies();
      cookie.set("accessToken", res.data.token.access);
      cookie.set("refreshToken", res.data.token.refresh);

      const decode = jwt_decode(res.data.token.access);
      console.log("login decode .", decode.nickname);

      // redux nickname 
      dispatch(GET_NAME(res.data.memberResponse.nickname));
      localStorage.setItem("user-id", res.data.memberResponse.id);
      localStorage.setItem("nickname", res.data.memberResponse.nickname);

      await sendTestRequest();
    } catch (error) {}
  };

  // test 
  //  GET 
  const sendTestRequest = async () => {
    try {
      const res = await authClient({
        method: "get",
        url: "/test",
      });

      console.log("Test Response:", res.data);
      // console.log(res);
      console.log(res.data.id);
    } catch (error) {
      console.error("Test Error:", error);
    }
  };

  // 
  const login = async (e) => {
    e.preventDefault();
    try {
      return new Promise((resolve, reject) => {
        if (!Kakao) {
          reject("Kakao   .");
        }
        Kakao.Auth.login({
          success: async (res) => {
            //  GET   
            const { access_token } = res;

            // snsLogin  
            await snsLogin(access_token);

            // test  
            // await sendTestRequest();

            // setIsLogin(true);
            //console.log(res);
            navigate("/MyPage");
          },
          fail: (err) => {
            console.error(err);
          },
        });
      });
    } catch (err) {
      console.error(err);
    }
  };

  useEffect(() => {
    if (Kakao.Auth.getAccessToken()) {
      setIsLogin(true);
    }
  }, []);

  return (
    <S.Container>
      <S.Wrapper>
        <S.Title>BeJuRyu   .</S.Title>
        <S.Form>
          <S.bejuryuImg src={logo} alt="logo"></S.bejuryuImg>
        </S.Form>
        <S.explainBox>
            ,     . ,
          BeJuRyu    ?
        </S.explainBox>
        <S.BtnList>
          <div>
            <div
              id="kakao-login-btn"
              onClick={login}
              style={{ cursor: "pointer" }}
            >
              <img
                src="https://k.kakaocdn.net/14/dn/btroDszwNrM/I6efHub1SN5KCJqLm1Ovx1/o.jpg"
                width="222"
                alt="  "
              />
            </div>
          </div>
        </S.BtnList>
      </S.Wrapper>
    </S.Container>
  );
}

export default Login;
