import S from "./styled";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import PhotoUpload from "./PhotoUpload";
import authClient from "../../apis/authClient";

function Recommend() {
  const navigate = useNavigate();
  const [jwtToken, setJwtToken] = useState("");
  const [imagePreview, setImagePreview] = useState(null);
  const [selectedFile, setSelectedFile] = useState(null);
  const [inputValue, setInputValue] = useState(""); //    

  const currentDate = getCurrentDateTime(); //   
  const handleFormSubmit = async (e) => {
    e.preventDefault();
    navigate("/result");
    if (selectedFile) {
      const reader = new FileReader();
      // base64 encoding
      reader.onloadend = async () => {
        // const arrayBuffer = reader.result;
        // const byteArray = new Uint8Array(arrayBuffer);

        const base64Data = String(reader.result.split(",")[1]);

        try {
          const res = await authClient({
            method: "post",
            url: "/api/analyze/sources",

            data: {
              date: currentDate,
              facialExpression: base64Data,
              textExpression: inputValue,
            },
            headers: {
              "Content-Type": "text/plain",
            },
          });
          if (res) {
            console.log(res.data);
          }
          //   
        } catch (error) {
          if (error.response) {
            //   
            const err = error.response.data;
            console.log(err);
            console.log(error.message);
          }
        }
      };

      reader.readAsDataURL(selectedFile);
      // const userId = localStorage.getItem("user-id");
      // console.log(typeof parseInt(userId, 10));
    } else {
      console.log("");
    }
  };

  const handleInputChange = (e) => {
    setInputValue(e.target.value);
  };

  const handleFileChange = (file) => {
    setSelectedFile(file);
    //setInputValue(e.target.value);
  };

  return (
    <S.Container>
      <S.Wrapper>
        <S.Title>      !</S.Title>
        <S.WhiteBox>
          <S.textInput value={inputValue} onChange={handleInputChange} />
          {inputValue === "" && (
            <S.MyState>
               ?     !
            </S.MyState>
          )}
        </S.WhiteBox>
        <PhotoUpload
          setSelectedFile={handleFileChange}
          setImagePreview={setImagePreview}
          imagePreview={imagePreview}
        />

        <S.SubmitButton onClick={handleFormSubmit}> </S.SubmitButton>
      </S.Wrapper>
    </S.Container>
  );
}

//    "yyyy.MM.dd HH:mm"   
function getCurrentDateTime() {
  const date = new Date();

  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, "0");
  const day = String(date.getDate()).padStart(2, "0");
  const hours = String(date.getHours()).padStart(2, "0");
  const minutes = String(date.getMinutes()).padStart(2, "0");

  const formattedDateTime = `${year}-${month}-${day} ${hours}:${minutes}`;

  return formattedDateTime;
}

export default Recommend;
