import S from "./styled";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";

function History() {
  const navigate = useNavigate();

  const goMypage = () => {
    navigate("/mypage");
  };
  const HistoryView = (
    <S.Container>
      <S.Wrapper>
        <S.Title>   </S.Title>
        <S.WhiteBox></S.WhiteBox>
        <S.ReButton onClick={goMypage}> </S.ReButton>
      </S.Wrapper>
    </S.Container>
  );

  return HistoryView;
}

export default History;
