import React from "react";
import S from "./styled";
import styled from "styled-components";
import { useNavigate, useLocation } from "react-router-dom";
import { useState, useEffect } from "react";
import { useDispatch } from "react-redux";
import { useParams } from "react-router-dom";
import Rating from "@mui/material/Rating";
import StarIcon from "@mui/icons-material/Star";
import Box from "@mui/material/Box";
import authClient from "../../apis/authClient";
import Container from "@mui/material/Container";
import { List, ListItem, ListItemText } from "@mui/material";
import axios from "axios";
import noAuthClient from "../../apis/noAuthClient";

const StyledList = styled(List)`
  display: flex;
  //flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  //justify-content: space-between;
  //border: 1px solid #ccc;
  padding: 10px;
  font-family: "BejuryuFont";
`;

const StyledListItem = styled.li`
  border-bottom: 1px solid #ccc;
  padding: 10px;
  flex: 1 0 48%;

  & span {
    font-family: "BejuryuFont";
  }
`;

const StyledImage = styled.img`
  width: 100px;
  height: 200px;
  object-fit: cover;
`;

function JuryuInfo() {
  const location = useLocation();
  const [value, setValue] = useState(2);
  const [hover, setHover] = useState(-1);
  //     
  const [inputValue, setInputValue] = useState("");
  // 
  const [decodedImage, setDecodedImage] = useState(null);
  const [drinkInfo, setDrinkInfo] = useState(null);
  //   
  const [drinkReviewList, setDrinkReviewList] = useState([]);
  //     pagination
  // const [startIndex, setStartIndex] = useState(0); //   
  // const [endIndex, setEndIndex] = useState(4); //   
  // const [showMore, setShowMore] = useState(false); //   
  const [page, setPage] = useState(1);
  const [perPage, setPerPage] = useState(4);
  const totalPages = Math.ceil(drinkReviewList.length / perPage);

  const [alertMessage, setAlertMessage] = useState("");

  //      
  const getCurrentPageItems = () => {
    const startIndex = (page - 1) * perPage;
    const endIndex = startIndex + perPage;
    return drinkReviewList.slice(startIndex, endIndex);
  };

  //    
  const goToNextPage = () => {
    if (page < totalPages) {
      setPage(page + 1);
    }
  };

  //    
  const goToPreviousPage = () => {
    if (page > 1) {
      setPage(page - 1);
    }
  };

  const juryuId = location.state?.juryuId;

  //   
  const decodeBase64 = (base64) => {
    try {
      const binaryString = window.atob(base64);
      const bytes = new Uint8Array(binaryString.length);
      for (let i = 0; i < binaryString.length; i++) {
        bytes[i] = binaryString.charCodeAt(i);
      }
      return URL.createObjectURL(
        new Blob([bytes.buffer], { type: "image/png" })
      );
    } catch (error) {
      console.error(error);
      return null;
    }
  };

  //   import
  useEffect(() => {
    const JuryuData = async () => {
      try {
        const response = await noAuthClient({
          method: "get",
          url: `/api/drinks/${juryuId}`,
        });
        //const { name, image, dosu, price } = response.data;
        //const decodedImage = decodeBase64(image)'

        setDrinkInfo(response.data);

        if (response) {
          console.log(response.data);
        }
      } catch (error) {
        console.error(error);
      }
    };

    const juryuScore = async () => {
      try {
        const response = await noAuthClient({
          method: "get",
          url: `/api/drinks/${juryuId}/rating`,
        });
        if (response) {
          console.log(response.data);
        }
      } catch (error) {
        console.log("juryuId error!", error.message);
      }
    };
    //console.log(drinkReviewList);

    const juryuReview = async () => {
      try {
        const response = await noAuthClient({
          method: "get",
          url: `/api/drinks/${juryuId}/reviews`,
        });

        if (response) {
          setDrinkReviewList(response.data.reviews);
        }
      } catch (error) {
        console.error(error);
      }
    };

    JuryuData();
    juryuScore();
    juryuReview();
  }, [juryuId]);

  const handleInputChange = (e) => {
    setInputValue(e.target.value);
  };

  const navigate = useNavigate();

  const labels = {
    0.5: "",
    1: " ",
    1.5: " ",
    2: " ",
    2.5: " ",
    3: " ?",
    3.5: " ",
    4: " ",
    4.5: "  ",
    5: "",
  };

  const dictionary = () => {
    navigate("/dictionary");
  };

  const ranking = () => {
    navigate("/MyPage");
  };
  function getLabelText(value) {
    return `${value} Star${value !== 1 ? "s" : ""}, ${labels[value]}`;
  }

  //    "yyyy.MM.dd HH:mm"   
  function getCurrentDateTime() {
    const date = new Date();

    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, "0");
    const day = String(date.getDate()).padStart(2, "0");
    const hours = String(date.getHours()).padStart(2, "0");
    const minutes = String(date.getMinutes()).padStart(2, "0");

    const formattedDateTime = `${year}-${month}-${day} ${hours}:${minutes}`;

    return formattedDateTime;
  }

  const currentDate = getCurrentDateTime(); //   

  const handleReviewSubmit = async (e, juryuId) => {
    e.preventDefault();
    //alert("  !");
    //navigate("/dictionary");

    const labelKey = parseInt(value);

    try {
      const res = await authClient({
        method: "post",
        url: `/api/drinks/${juryuId}/reviews`,
        data: {
          userId: localStorage.getItem("user-id"),
          comment: inputValue,
          score: labelKey * 2,
          date: currentDate,
        },
      });
      if (res) {
        console.log(res.data);
      }
    } catch (error) {
      if (error.response) {
        const err = error.response.data;
        console.log(err);
        console.log(error.message);
      } else {
        console.log("ERROR:", error.message);
      }
    }

    setAlertMessage(".    !");
  };

  return (
    <S.Container>
      <S.Wrapper>
        <S.WhiteBox>
          <S.CenteredFormBox>
            <S.Title></S.Title>
            <StyledImage
              src={drinkInfo && decodeBase64(drinkInfo.image)}
              alt=" "
            />
            <StyledList>
              <StyledListItem>
                <ListItemText primary={drinkInfo?.name} secondary=" " />
              </StyledListItem>
              <StyledListItem>
                <ListItemText primary={drinkInfo?.dosu} secondary="" />
              </StyledListItem>
              <StyledListItem>
                <ListItemText primary={drinkInfo?.price} secondary="" />
              </StyledListItem>
              <StyledListItem>
                <ListItemText primary={drinkInfo?.type} secondary="" />
              </StyledListItem>
            </StyledList>
          </S.CenteredFormBox>
          <S.FormBox>
            <S.Title>Be   </S.Title>

            {getCurrentPageItems().map((reviews) => (
              <p key={reviews.id}>
                {reviews.comment}{" "}
                <p>
                  {reviews.nickname} {reviews.date}
                </p>
              </p>
            ))}
            <S.ButtonContainer>
              {/*     */}
              {page > 1 && (
                <S.PageButton onClick={goToPreviousPage}>
                   
                </S.PageButton>
              )}

              {/*    */}
              {page < totalPages && (
                <S.PageButton onClick={goToNextPage}> </S.PageButton>
              )}
            </S.ButtonContainer>
          </S.FormBox>

          <S.ReviewBox>
            <S.Title> !</S.Title>
            <Box
              sx={{
                width: 200,
                display: "flex",
                alignItems: "center",
              }}
            >
              <Rating
                name="hover-feedback"
                value={value}
                precision={0.5}
                getLabelText={getLabelText}
                onChange={(event, newValue) => {
                  setValue(newValue);
                }}
                onChangeActive={(event, newHover) => {
                  setHover(newHover);
                }}
                emptyIcon={
                  <StarIcon
                    style={{ opacity: 0.55, color: "action" }}
                    fontSize="inherit"
                    sx={{
                      color: "yellow",
                    }}
                  />
                }
              />
              {value !== null && (
                <Box sx={{ ml: 2 }}>{labels[hover !== -1 ? hover : value]}</Box>
              )}
            </Box>
            <S.textInput
              value={inputValue}
              onChange={handleInputChange}
            ></S.textInput>
            <S.ReButton onClick={(e) => handleReviewSubmit(e, juryuId)}>
               
            </S.ReButton>
            {alertMessage && <p>{alertMessage}</p>}
            {/* <S.ReButton> </S.ReButton> */}
          </S.ReviewBox>
        </S.WhiteBox>
        <S.ButtonContainer>
          <S.ReButton onClick={dictionary}>  </S.ReButton>
          <S.ReButton onClick={ranking}>BE </S.ReButton>
        </S.ButtonContainer>
      </S.Wrapper>
    </S.Container>
  );
}

export default JuryuInfo;
